"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Skeleton } from "@/components/ui/skeleton";
import { useWalletStore } from "@/lib/wallet-provider";
import { formatDate, formatCurrency } from "@/lib/utils";
import { QRCodeCanvas } from "qrcode.react";
import { toast } from "sonner";
import {
  ArrowLeft,
  QrCode,
  Download,
  CheckCircle,
  Clock,
  Truck,
  Store,
  Package,
  Leaf,
  MapPin,
  Calendar,
  Search,
  Filter,
  ExternalLink,
  TrendingUp,
  BarChart3,
  Eye,
  RefreshCw,
} from "lucide-react";

// Types
interface HarvestToken {
  id: string;
  yieldPredictionId?: string;
  cropType: string;
  amount: number;
  tokenizedAmount: number;
  qualityGrade?: string;
  status: "pending" | "tokenized" | "sold" | "burned" | "locked";
  isLocked: boolean;
  qrCode: string;
  metadataURI?: string;
  blockchainTokenId?: string;
  blockchainTxHash?: string;
  createdAt: string;
}

interface SupplyChainEvent {
  id: string;
  tokenId: string;
  stage: string;
  name: string;
  description: string;
  status: "completed" | "in-progress" | "pending";
  date: string;
  location: string;
  details: string;
  transactionHash?: string;
  createdAt: string;
}

export default function Tracker() {
  const router = useRouter();
  const { isConnected, account } = useWalletStore();

  // State
  const [harvestTokens, setHarvestTokens] = useState<HarvestToken[]>([]);
  const [supplyChainEvents, setSupplyChainEvents] = useState<SupplyChainEvent[]>([]);
  const [selectedToken, setSelectedToken] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<string>("all");
  const [cropFilter, setCropFilter] = useState<string>("all");

  // Fetch data functions
  const fetchHarvestTokens = async () => {
    if (!account?.address) return;

    try {
      const response = await fetch(
        `/api/harvest-tokens?walletAddress=${account.address}`
      );
      if (response.ok) {
        const result = await response.json();
        setHarvestTokens(result.data || []);
      }
    } catch (error) {
      console.error("Error fetching harvest tokens:", error);
      toast.error("Failed to load harvest tokens");
    }
  };

  const fetchSupplyChainEvents = async (tokenId: string) => {
    try {
      // For now, generate mock supply chain events based on token data
      // In the future, this will come from a real API endpoint
      const mockEvents: SupplyChainEvent[] = [
        {
          id: "1",
          tokenId,
          stage: "farm",
          name: "Farm Harvest",
          description: "Harvest collected from farm",
          status: "completed",
          date: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString(),
          location: "Farm Location",
          details: "Crop harvested and quality tested",
          createdAt: new Date().toISOString(),
        },
        {
          id: "2",
          tokenId,
          stage: "processing",
          name: "Processing",
          description: "Crop processed, cleaned, and packaged",
          status: "completed",
          date: new Date(Date.now() - 6 * 24 * 60 * 60 * 1000).toISOString(),
          location: "Processing Center",
          details: "Grain cleaned, sorted, and packaged",
          createdAt: new Date().toISOString(),
        },
        {
          id: "3",
          tokenId,
          stage: "tokenized",
          name: "Tokenized",
          description: "Harvest converted to blockchain tokens",
          status: "completed",
          date: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000).toISOString(),
          location: "Hedera Network",
          details: "Tokens minted on Hedera blockchain",
          createdAt: new Date().toISOString(),
        },
        {
          id: "4",
          tokenId,
          stage: "transport",
          name: "Transport",
          description: "Goods in transit to market",
          status: "in-progress",
          date: new Date().toISOString(),
          location: "En route",
          details: "GPS tracking active",
          createdAt: new Date().toISOString(),
        },
        {
          id: "5",
          tokenId,
          stage: "market",
          name: "Market Delivery",
          description: "Goods delivered to market",
          status: "pending",
          date: new Date(Date.now() + 2 * 24 * 60 * 60 * 1000).toISOString(),
          location: "Market",
          details: "Scheduled for delivery",
          createdAt: new Date().toISOString(),
        },
      ];
      setSupplyChainEvents(mockEvents);
    } catch (error) {
      console.error("Error fetching supply chain events:", error);
    }
  };

  const fetchData = async () => {
    setIsLoading(true);
    await fetchHarvestTokens();
    setIsLoading(false);
  };

  useEffect(() => {
    if (!isConnected) {
      router.push("/");
    } else if (account?.address) {
      fetchData();
    }
  }, [isConnected, router, account?.address]);

  useEffect(() => {
    if (selectedToken) {
      fetchSupplyChainEvents(selectedToken);
    }
  }, [selectedToken]);

  // Helper functions
  const getStageIcon = (stage: string) => {
    const icons: { [key: string]: any } = {
      farm: Leaf,
      processing: Package,
      tokenized: QrCode,
      transport: Truck,
      market: Store,
    };
    return icons[stage] || Package;
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "completed":
        return "bg-green-100 text-green-600 dark:bg-green-900";
      case "in-progress":
        return "bg-blue-100 text-blue-600 dark:bg-blue-900";
      case "pending":
        return "bg-gray-100 text-gray-400 dark:bg-gray-800";
      default:
        return "bg-gray-100 text-gray-400 dark:bg-gray-800";
    }
  };

  // Filter tokens
  const filteredTokens = harvestTokens.filter((token) => {
    const matchesSearch = token.cropType
      .toLowerCase()
      .includes(searchTerm.toLowerCase());
    const matchesStatus = statusFilter === "all" || token.status === statusFilter;
    const matchesCrop = cropFilter === "all" || token.cropType === cropFilter;
    return matchesSearch && matchesStatus && matchesCrop;
  });

  // Get unique crop types for filter
  const uniqueCrops = [...new Set(harvestTokens.map((token) => token.cropType))];

  // Statistics
  const stats = {
    totalTokens: harvestTokens.length,
    tokenizedTokens: harvestTokens.filter((t) => t.status === "tokenized").length,
    totalValue: harvestTokens.reduce((sum, token) => sum + token.tokenizedAmount, 0),
    lockedTokens: harvestTokens.filter((t) => t.isLocked).length,
  };

  const handleDownloadQR = () => {
    if (!selectedToken) return;

    const canvas = document.getElementById("qr-code") as HTMLCanvasElement;
    if (canvas) {
      const link = document.createElement("a");
      link.download = `harvest-token-${selectedToken}.png`;
      link.href = canvas.toDataURL();
      link.click();
    }
  };

  const selectedTokenData = harvestTokens.find(
    (token) => token.id === selectedToken
  );

  if (!isConnected) {
    return null;
  }

  return (
    <div className="max-w-6xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Button
          variant="outline"
          size="sm"
          onClick={() => router.push("/dashboard")}
        >
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back
        </Button>
        <div>
          <h1 className="text-3xl font-bold text-foreground">
            Supply Chain Tracker
          </h1>
          <p className="text-muted-foreground">
            Track your harvest from farm to market
          </p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Token Selection */}
        <Card className="dashboard-card">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Package className="h-5 w-5" />
              Select Harvest Token
            </CardTitle>
            <CardDescription>
              Choose a tokenized harvest to track
            </CardDescription>
          </CardHeader>
          <CardContent>
            {harvestTokens.length > 0 ? (
              <div className="space-y-3">
                {harvestTokens.map((token) => (
                  <div
                    key={token.id}
                    className={`p-4 border rounded-lg cursor-pointer transition-colors ${
                      selectedToken === token.id
                        ? "border-blue-500 bg-blue-50 dark:bg-blue-950"
                        : "border-gray-200 dark:border-gray-700 hover:border-gray-300"
                    }`}
                    onClick={() => setSelectedToken(token.id)}
                  >
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="font-semibold">{token.cropType}</h3>
                        <p className="text-sm text-gray-600 dark:text-gray-300">
                          {token.amount} tons • {token.tokenizedAmount} tokens
                        </p>
                      </div>
                      <Badge
                        variant={
                          token.status === "tokenized" ? "default" : "secondary"
                        }
                      >
                        {token.status}
                      </Badge>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-8">
                <Package className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-600 dark:text-gray-300 mb-4">
                  No tokenized harvests yet. Tokenize your harvest first.
                </p>
                <Button onClick={() => router.push("/lending")}>
                  Tokenize Harvest
                </Button>
              </div>
            )}
          </CardContent>
        </Card>

        {/* QR Code */}
        {selectedTokenData && (
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <QrCode className="h-5 w-5" />
                QR Code
              </CardTitle>
              <CardDescription>
                Scan to verify harvest authenticity
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center">
              <div className="p-4 bg-white dark:bg-gray-800 rounded-lg inline-block">
                <QRCodeCanvas
                  id="qr-code"
                  value={selectedTokenData.qrCode}
                  size={200}
                  level="H"
                  includeMargin={true}
                />
              </div>
              <div className="mt-4 space-y-2">
                <p className="text-sm text-gray-600 dark:text-gray-300">
                  Token ID: {selectedTokenData.id}
                </p>
                <p className="text-sm text-gray-600 dark:text-gray-300">
                  Crop: {selectedTokenData.cropType}
                </p>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleDownloadQR}
                  className="mt-2"
                >
                  <Download className="h-4 w-4 mr-2" />
                  Download QR
                </Button>
              </div>
            </CardContent>
          </Card>
        )}
      </div>

      {/* Supply Chain Timeline */}
      {selectedTokenData && (
        <Card className="mt-8">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Truck className="h-5 w-5" />
              Supply Chain Timeline
            </CardTitle>
            <CardDescription>
              Track the journey of your {selectedTokenData.cropType} harvest
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-6">
              {supplyChainStages.map((stage, index) => {
                const Icon = stage.icon;
                const isCompleted = stage.status === "completed";
                const isInProgress = stage.status === "in-progress";
                const isPending = stage.status === "pending";

                return (
                  <div key={stage.id} className="flex items-start gap-4">
                    {/* Timeline Line */}
                    <div className="flex flex-col items-center">
                      <div
                        className={`w-12 h-12 rounded-full flex items-center justify-center ${
                          isCompleted
                            ? "bg-green-100 text-green-600 dark:bg-green-900"
                            : isInProgress
                            ? "bg-blue-100 text-blue-600 dark:bg-blue-900"
                            : "bg-gray-100 text-gray-400 dark:bg-gray-800"
                        }`}
                      >
                        {isCompleted ? (
                          <CheckCircle className="h-6 w-6" />
                        ) : isInProgress ? (
                          <Clock className="h-6 w-6" />
                        ) : (
                          <Icon className="h-6 w-6" />
                        )}
                      </div>
                      {index < supplyChainStages.length - 1 && (
                        <div
                          className={`w-0.5 h-16 ${
                            isCompleted
                              ? "bg-green-200 dark:bg-green-800"
                              : "bg-gray-200 dark:bg-gray-700"
                          }`}
                        />
                      )}
                    </div>

                    {/* Stage Content */}
                    <div className="flex-1 pb-8">
                      <div className="flex items-center justify-between mb-2">
                        <h3 className="font-semibold text-lg">{stage.name}</h3>
                        <Badge
                          variant={
                            isCompleted
                              ? "default"
                              : isInProgress
                              ? "secondary"
                              : "outline"
                          }
                        >
                          {stage.status}
                        </Badge>
                      </div>
                      <p className="text-gray-600 dark:text-gray-300 mb-3">
                        {stage.description}
                      </p>
                      <p className="text-sm text-gray-500 dark:text-gray-400 mb-3">
                        {stage.details}
                      </p>
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                        <div className="flex items-center gap-2">
                          <Calendar className="h-4 w-4 text-gray-400" />
                          <span className="text-gray-600 dark:text-gray-300">
                            Date:
                          </span>
                          <span className="font-medium">{stage.date}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <MapPin className="h-4 w-4 text-gray-400" />
                          <span className="text-gray-600 dark:text-gray-300">
                            Location:
                          </span>
                          <span className="font-medium">{stage.location}</span>
                        </div>
                      </div>
                      {isInProgress && (
                        <div className="mt-3 p-3 bg-blue-50 dark:bg-blue-950 rounded-lg">
                          <div className="flex items-center gap-2">
                            <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
                            <span className="text-sm text-blue-700 dark:text-blue-300 font-medium">
                              Live tracking active
                            </span>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Blockchain Verification */}
      {selectedTokenData && (
        <Card className="mt-8">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <QrCode className="h-5 w-5" />
              Blockchain Verification
            </CardTitle>
            <CardDescription>
              Verify the authenticity of your harvest tokens
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div className="p-4 bg-green-50 dark:bg-green-950 rounded-lg">
                  <h3 className="font-semibold text-green-800 dark:text-green-200 mb-2">
                    Token Details
                  </h3>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Token ID:
                      </span>
                      <span className="font-mono">{selectedTokenData.id}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Crop Type:
                      </span>
                      <span className="font-medium">
                        {selectedTokenData.cropType}
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Amount:
                      </span>
                      <span className="font-medium">
                        {selectedTokenData.amount} tons
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Tokens:
                      </span>
                      <span className="font-medium">
                        {selectedTokenData.tokenizedAmount}
                      </span>
                    </div>
                  </div>
                </div>
              </div>

              <div className="space-y-4">
                <div className="p-4 bg-blue-50 dark:bg-blue-950 rounded-lg">
                  <h3 className="font-semibold text-blue-800 dark:text-blue-200 mb-2">
                    Blockchain Info
                  </h3>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Network:
                      </span>
                      <span className="font-medium">Hedera</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Token Standard:
                      </span>
                      <span className="font-medium">HTS</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Status:
                      </span>
                      <Badge variant="default">
                        {selectedTokenData.status}
                      </Badge>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600 dark:text-gray-300">
                        Verification:
                      </span>
                      <span className="text-green-600 font-medium">
                        ✓ Verified
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
